import { Table, Input, Button,Label } from 'reactstrap';
import {useNavigate, useParams} from "react-router-dom";
import {useEffect, useState} from "react";
import axios from "axios";
import {url} from '../config';


const DetailForm = () => {
    const {num} = useParams();
    const [board, setBoard] = useState({
        num:null, subject:'', content:'', writedate:'', fileurl:"",writer:'',viewcount:null, likecount:null
    });
    const [images, setImages] = useState([]);
    const navigate = useNavigate();
    const [heart, setHeart] = useState(false);
    const [likeCount, setLikeCount] = useState(0);

    useEffect(()=>{
        axios.get(`${url}/boarddetail/${num}`)
            .then(res=> {
                console.log(res.data);
                setBoard(res.data.board);
                let fileurl = res.data.board.fileurl;
                if(fileurl!=null && fileurl!=='') {
                    let filenums = fileurl.split(',');
                    setImages([...filenums]);
                }
                setHeart(res.data.heart);
            })
            .catch(err=>{
                console.log(err);
            })
    }, [])

    const boardModify = (boardNum) => {
        navigate(`/modifyform/${boardNum}`);
    }

    const selectBoard = (e) => {
        axios.get(`${url}/boardlike/${num}`)
            .then(res => {
                console.log(res.data)
                setBoard({...board, likecount:res.data.likeCount});
                setHeart(res.data.isSelect);
            })
            .catch(err => {
                console.log(err);
            })
    }


    return (
        <>
            <h5 style={{textAlign:'center', margin:'20px auto'}}></h5>
            <div style={{margin:'0 auto',width:'600px', border:'1px solid lightgray', borderRadius:'7px', padding:'10px'}}>
                <Table borderless>
                    <tbody>
                        <tr>
                            <td><Label for="writer"></Label></td>
                            <td><Input type="text" name="writer"
                                id="writer" disabled value={board.writer} /></td>
                        </tr>
                        <tr>
                            <td><Label for="subject"> </Label></td>
                            <td><Input name="subject" type="text"
                                id="subject" disabled value={board.subject} /></td>
                        </tr>
                        <tr>
                            <td><Label for="content"> </Label></td>
                            <td><Input type='textarea' id="content" name="content"
                                cols="40" rows="15" disabled value={board.content}/></td>
                        </tr>
                        <tr>
                            <td><Label for="file"></Label></td>
                            <td>
                                {images.length!==0 && images.map(num =>
                                    <img key={num} src={`http://localhost:8088/img/${num}`} alt=''
                                         width="100px" style={{marginRight:"10px"}}/>
                                    )
                                }
                            </td>
                        </tr>
                        <tr>
                            <td></td>
                            <td>
                                <Button color='primary' onClick={()=>boardModify(board.num)}></Button>&nbsp;&nbsp;
                                <Button color='primary' tag='a' href='/boardlist/1'></Button>
                                <img src={heart?"/afterlike.png":"/beforelike.png"} alt="" width="30px"
                                    onClick={()=>selectBoard}/> &nbsp;
                                {board.likecount}
                            </td>
                        </tr>
                    </tbody>
                </Table>
            </div>
        </>
    )
}
export default DetailForm;